body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    min-height: 100vh;
}

#root {
    min-height: 100vh;
}


a.hide-link:link {
    text-decoration: none;
}
a.hide-link:visited {
    text-decoration: none;
}
a.hide-link:hover {
    text-decoration: none;
}
a.hide-link:hover .title {
    text-decoration: underline;
}
a.hide-link:active .title{
    text-decoration: underline;
}
a.hide-link:active{
    text-decoration: none;
}
a:hover h2.link-with-underline {
    text-decoration: underline;
}

.App {
  text-align: center;
  background-color: #eeeeee;
  min-height: 100vh;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

p {
  text-align: justify;
  text-justify: inter-word;
}
.app-content {
  min-height: 100vh;
}
.rotate-180 {
  -webkit-transform: rotate(180deg);
  transform: rotate(180deg);
}

.header-logo {
  max-height: 50px;
}

.app-parent {
  min-height: 100vh;
  padding-bottom: 5rem;
}

.gdprBanner {
    position: fixed;
    bottom: 0;
    background-color: #cccccc;
    opacity: 0.9;
    min-height: 130px;
    width: 100%;
    padding: 1rem 5rem;
    z-index: 1000;
}

.gdprBanner > button {
    margin-top: 1rem;
}


@media (max-width: 1100px) {
    .gdprBanner {
        padding: 1rem 1rem;
    }
}

.filling-empty-space {
    width:412px;
    height:0;
}

.search-bar {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    margin-top: 3rem;
    padding-left: 20vw;
    padding-right: 20vw;
    margin-bottom: 2rem;
}

.search-bar > button, .search-bar > label {
    padding-left: 1.1rem;
    padding-right: 1.1rem;
}

.search-bar-icon {
    display: flex;
    align-items: center;
}

.search-bar-icon > .material-icons {
    margin-top: -6px;
}

.search-tags {
    min-width: 330px;
}

.article-wrapper {
    max-width: 1000px;
    margin-left: 0;
    min-width: 100%;
    padding: 1rem 1rem 3rem;
}

.similars-list-wrapper {
    padding-top: 2rem;
    margin-bottom: 3.5rem;
    width: 100%;
    border-top: 1px solid #ccc;
}

.similars-list {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    scroll-behavior: smooth;
}

/* Ecrans > 1084px */
@media not screen and (max-width: 1084px) {
    .article-wrapper {
        padding: 1rem 2rem 3rem;
        min-width: 70%;
    }
}
/* Ecrans > 1400 */
@media not screen and (max-width: 1400px) {
    .similars-list-wrapper {
        width: calc(100% - 65% - 8% - 1rem);
        margin-left: -1rem;
        overflow: hidden;
        position: absolute;
        left: calc(65% + 1rem + 8%);
        top: auto;
        border-top: none;
        border-left: 1px solid #ccc;
        padding-left: 10px;
    }
    .article-page-wrapper {
        justify-content: initial !important;
    }
    .similars-list {
        width: calc(100% + 20px);
        overflow-y: auto;
        max-height: calc(100vh - 64px - 52px - 2rem);
        padding-right: 20px;
        border-bottom: 1px dashed #ccc;
    }
}
/* Ecrans > 1600 */
@media not screen and (max-width: 1600px) {
    .article-wrapper {
        margin-left: 10%;
        min-width: 60%;
    }
    .similars-list-wrapper {;
        margin-left: 0;
    }
}

.article-page-wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

.article-content {
    min-width: 300px;
    font-size: 1.25rem;
}

.article-content-text {
    margin-top: 5.5rem;
}

.article-content p {
    margin-top: 2rem;
    line-height: 2rem;
    text-indent: 5em;
}

.article-content h2, .article-content h3 {
    margin-top: 3.5rem;
}

.article-wrapper img {
    width: auto;
    max-width: 100%;
    height: auto;
}

.article{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-content: flex-start;
}

.article-footer {
    width: 100%;
    text-align: left;
    margin-top: 6rem;
}

.article-footer-tag-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    margin-top: 3rem;
}

.article-footer-tag-list > a {
    margin-bottom: 1rem;
}

.article-footer-votes {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}

.article-description {
    color: black;
}


.article-infos {
    text-align: left;
    width: 100%;
    font-size: 12px;
    margin-bottom: 2rem;
    margin-top: 1rem;
}

.folder-children-list {
    margin-top: 3rem;
}
.folder-children-list-hr {
    margin-top: 1.5rem;
}

.folder-brother-list {
    margin-top: 4rem;
    margin-bottom: -3.5rem;
}

.img-principale {
    margin-bottom: -3.5rem;
}

.brother-list {
    margin-top: 1.5rem;
    margin-bottom: .5rem;
}
.brother-list > div > div, .brother-list > div > a{
    max-width: 50%;
}
.brother-list > div span.material-icons{
    margin-right: 1rem;
    margin-left: 1rem;
}
.brother-list > div a span:not(.material-icons){
    text-decoration: underline !important;
}

.folder-name {
    margin-top: 1rem;
}

.donation-button {
    margin-top: 4rem;
    width: 100%;
}
.donation-button > div {
    padding: 2rem 1rem 2rem 1rem;
}
.donation-button form {
    margin-top: 1rem;
    margin-bottom: 1rem;
}
.image-legend {
    margin-bottom: 3.5rem;
}

p {
    font-size: 1rem !important;
}

h2 {
    font-size: 1.5rem !important;
    font-weight: 400 !important;
}

h3 {
    font-size: 1.25rem !important;
    font-weight: 400 !important;
}

.mt-2 {
    margin-top: 2rem;
}
.text-align-left {
    text-align: left;
}
