.filling-empty-space {
    width:412px;
    height:0;
}

.search-bar {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    margin-top: 3rem;
    padding-left: 20vw;
    padding-right: 20vw;
    margin-bottom: 2rem;
}

.search-bar > button, .search-bar > label {
    padding-left: 1.1rem;
    padding-right: 1.1rem;
}

.search-bar-icon {
    display: flex;
    align-items: center;
}

.search-bar-icon > .material-icons {
    margin-top: -6px;
}

.search-tags {
    min-width: 330px;
}
